<template>
  <div class="seat-designer-page" :style="canvasCss" @mousedown.left="preRegion">
    <region :visible="regionVisible" :region="regionRect"></region>
    <div id="panel_outer" :style="panelSty">
      <slot name="seat"></slot>
      <div id="panel_bg" class="ds_seat_panel_bg"></div>
    </div>
    <slot name="seatPop"></slot>
  </div>
</template>

<script>
import {mapState} from 'vuex';
import region from "_platform/designer/seat/printingDesign/region"

export default {
  name: "Canvas",
  components: {
    region,
  },
  data() {
    return {
      regionVisible: false,
      regionRect: {},   //
    }
  },

  computed: {
    ...mapState('printingDesign', {
      compelPanelSize: "compelPanelSize",
      panelSize: "panelSize",
      canvasCss(state) {
        const {width, height} = state.compelPanelSize;
        let _wid = width ? `${width}px` : '100%';
        let _hit = height ? `${height}px` : '100%';
        return {width: _wid, height: _hit}
      },
      panelParentSize(state) {
        let {width, height} = state.compelPanelSize;
        console.log(width)
        console.log(height)
        width = width || $(".seat-designer-page").width();
        height = height || $(".seat-designer-page").height();
        return {
          width: width,
          height: height
        }
      }
    }),
    panelSty() {
      return {
        width: this.panelSize.width ? this.panelSize.width + "px" : "100%",
        height: this.panelSize.height ? this.panelSize.height + "px" : "100%",
        left: this.panelSize.left + "px",
        top: this.panelSize.top + "px"
      }
    },
  },
  watch: {
    "compelPanelSize": {
      handler({width, height}) {
        const panelSize = {
          width: width,
          height: height,
          scale: 1,
          left: 0,
          top: 0
        }

        this.$store.commit("printingDesign/changePanelSize", panelSize);
      },
      deep: true

    },
  },
  mounted() {
    const self = this;
    $(".seat-designer-page")[0].addEventListener('mousewheel', self.handleScroll);

  },
  methods: {
    handleScroll(evt) {
      if (!evt.ctrlKey) return;
      const self = this;
      let wheelAction = evt.wheelDelta < 0 ? "down" : "up";

      //panel_outer
      let pos = dsf.math.convertLocalCoordinate("#panel_outer", {
        x: evt.pageX,
        y: evt.pageY
      });

      let {width: pWid, height: pHit} = self.panelParentSize;

      //
      let w = pWid * self.panelSize.scale;
      let h = pHit * self.panelSize.scale;

      //
      let rw = pos.x / w;
      let rh = pos.y / h;

      let newS = self.panelSize.scale;
      if (wheelAction === "up") {
        newS += 0.1;
      } else if (wheelAction === "down") {
        newS -= 0.1;
      }

      //0.1
      if (newS >= 0.1) {
        let panel = $("#panel_outer");
        let sw = pWid * newS;
        let sh = pHit * newS;
        let l = panel.position().left + pos.x - (sw * rw);
        let t = panel.position().top + pos.y - (sh * rh);
        const panelSize = {
          scale: Math.round(newS * 10) / 10,
          width: Math.floor(sw),
          height: Math.floor(sh),
          left: Math.floor(l),
          top: Math.floor(t)
        };
        self.$store.commit("printingDesign/changePanelSize", panelSize);
      }

      evt.preventDefault();
      evt.stopPropagation();
    },
    preRegion(evt) {
      const self = this;
      self.$store.commit("printingDesign/changeActiveId", "");
      let downPos = dsf.math.convertLocalCoordinate('.seat-designer-page', { x: evt.pageX, y: evt.pageY });
      function handleMouseMove(evt){
        self.regionVisible = true;
        let movePos = dsf.math.convertLocalCoordinate('.seat-designer-page', { x: evt.pageX, y: evt.pageY });
        self.$set(self.regionRect, "left", downPos.x > movePos.x? movePos.x: downPos.x);
        self.$set(self.regionRect, "top", downPos.y > movePos.y? movePos.y: downPos.y);
        self.$set(self.regionRect, "width", Math.abs(movePos.x - downPos.x));
        self.$set(self.regionRect, "height",Math.abs(movePos.y - downPos.y));
      }
      function handleMouseUp(){
        $(document).off("mousemove.region");
        self.$store.commit("printingDesign/changeRegionPosition", self.regionRect);
        self.regionVisible = false;
        self.regionRect = {};
      }

      $(document).off("mousemove.region");
      $(document).on("mousemove.region", function (evt) {
        handleMouseMove(evt);
      })
      $(document).one("mouseup.region", function () {
        handleMouseUp();
      })

    },
  },

}
</script>