<template>
  <div v-if="visible" class="region" :style="regionSty"></div>
</template>

<script>
export default {
  name: "Region",
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    region: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      test: false
    }
  },
  computed: {
    regionSty() {
      return {
        left: this.region.left + "px",
        top: this.region.top + "px",
        width: this.region.width + "px",
        height: this.region.height + "px",
      }
    }
  },
  methods: {

  }
}
</script>