<template>
  <div class="ds-designer ds-seat-container ds-seat-design">
    <div class="designer-head">
      <top-tools v-bind.sync="postData" @save-layout="saveLayout"></top-tools>
    </div>
    <div class="designer-content">
      <div class="seat-designer-center">
        <left-tools></left-tools>
        <print-canvas>
          <template v-slot:seat>
            <seat-item v-bind.sync="postData"></seat-item>
          </template>
        </print-canvas>
        <right-tools></right-tools>
      </div>
    </div>
  </div>
</template>

<script>
import topTools from "_platform/designer/seat/printingDesign/topTools.vue";
import leftTools from "_platform/designer/seat/printingDesign/leftTools.vue";
import rightTools from "_platform/designer/seat/printingDesign/rightTools.vue"
import canvas from "_platform/designer/seat/printingDesign/canvas.vue";
import seatItem from "_platform/designer/seat/printingDesign/seatItem";
  
import {changePanelSizeForBgImage, sysBindShortKey} from "_platform/designer/seat/func";
import ConfigBuilder from "_platform/designer/seat/util/configBuilder.js";


export default {
  name: "DsfSeatDesign",
  components: {
    topTools,
    leftTools,
    rightTools,
    "PrintCanvas": canvas,
    seatItem,
  },
  data() {
    return {
      keyStack: [],
        
      //props
      postData: {
        _id: "",
          
        //top-tools
        backgroundImage: {},
        canvasSet: {
          width: undefined,
          height: undefined
        },
        hasBg: false,
          
        //seat-item
        items: [],
      }
    }
  },
  computed: {},
  watch:{
    "postData.backgroundImage"(){
      changePanelSizeForBgImage.call(this);
    }
  },
    
  created() {
    const layoutUrl = dsf.url.getWebPath("$/seat_config/seat/printingDesign.js");
      
    this.$http.importFiles(layoutUrl).then((res) => {
      
      this.layoutConfig = new ConfigBuilder(printingDesignConfig, this.$route.query.opt);
      this.bindShortKey();
      this.getSeatInfo();
    });
  },
  beforeDestroy() {
    $(document).off("keydown");
    $(document).off("keyup");
  },
    
  methods: {
    saveLayout() {
      if(process.env.NODE_ENV === "development"){
        this.saveByDev();
      }
      else {
        this.saveByPub();
      }
    },
    bindShortKey() {
      const stopList = ["ctrl", "ctrl-d", "ctrl-s"];
      sysBindShortKey(this.keyStack, stopList);
    },
    saveByDev(){
      const purePostData = getPurePostData.call(this);
      const id = this.$route.query.id || dsf.uuid();
      purePostData._id = id;
        
      localStorage.setItem("printing_"+id, JSON.stringify(purePostData));
      const originQuery = this.$route.query;
      const _query = Object.assign({}, originQuery, {id: id})
      this.$router.replace({path: this.$route.path, query: _query})
    },
    saveByPub(){
      const purePostData = getPurePostData.call(this);
      const data = {
        "pageName": "edit",
        "namespace": "dsfa.printing.designcontent",
        "data": JSON.stringify(purePostData)
      }
      const loadVm = dsf.layer.loading();
      this.$http.post(this.layoutConfig.saveLayoutUrl, data)
        .done(res => {
          if (!res.success) {
            dsf.layer.message("", false);
            return
          }
            
          this.postData._id = res.data._id;
          this.$router.replace({path: this.$route.path, query: {id: res.data._id}})
          dsf.layer.message("");
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          dsf.layer.closeLoading(loadVm);
        })
    },
      
    getSeatInfo() {
      if (process.env.NODE_ENV === "development"){
        this.getInfoDev()
      }
      else {
        this.getInfoPub()
      }
    },
    getInfoDev(){
      const id = this.$route.query.id;
      if (!id) return;
      const data = JSON.parse(localStorage.getItem("printing_" + id));
      if (data){
        if (_.isEmpty(data.canvasSet)){
          data.canvasSet = {
            width: undefined,
            height: undefined
          }
        }
        this.postData = data;
      }
    },
    getInfoPub(){
      const id = this.$route.query.id;
    }
  },
}
  
function getPurePostData() {
  const purePostData = _.cloneDeep(this.postData);
  purePostData.items.forEach(el => {
    el.isFrozenSty = false
  })
  return purePostData;
}

</script>

<style type="text/scss" lang="scss">
  @import "~_platform/assets/styles/designer/seatDesign.scss";
</style>