<template>
  <div class="designer-left">
    <div class="designer-left-tools">
      <el-collapse v-for="(group, $index) in leftMenu.group" :key="$index" v-model="leftMenu.activeName">
        <el-collapse-item :title="group.title" :name="group.title">
          <div class="ds-tools-group-content" style="padding: 10px;">
            <div v-for="(item, itemindex) in group.items" :key="itemindex" class="ds-tool-item" :name="item.name" :data-type="item.type">
              {{ item.name }}
            </div>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: "LeftTools",
  data() {
    return {
      leftMenu: {
        activeName: ["", "", ""],
        group: [
          {
            title: '',
            items: [
              {
                name: "",
                type: "text"
              },
              {
                name: "",
                type: "image",
              }
            ]
          },
        ]
      },
      originWidth: 150,
      originHeight: 150,
    }
  },
  computed: {
    ...mapState("printingDesign", {
      panelSize: "panelSize",
      compelPanelSize: "compelPanelSize"
    }),
  },
  mounted() {
    const self = this;
    $(".ds-tool-item").draggable({
      addClasses: false,
      helper() {
        let txt = $(this).text();
        return $(`<div class="drag-item">${txt}</div>`)
      },
      cursor: "default",
      cursorAt: {
        top: -5,
        left: -5
      },
      stop(evt, ui) {
        //
        const pos = dsf.math.convertLocalCoordinate(".seat-designer-page", { x: evt.pageX, y: evt.pageY });
        const canvasWid = self.compelPanelSize.width || $(".seat-designer-page").width();
        const canvasHit = self.compelPanelSize.height || $(".seat-designer-page").height();

        //
        if ((pos.x >= 0 && pos.x <= canvasWid) && (pos.y >= 0 && pos.y <= canvasHit)) {
          const type = $(evt.target).data("type");

          //seatpanel_outer 
          const pos = dsf.math.convertLocalCoordinate("#panel_outer", {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);

          const basic = {
            id: dsf.uuid(),
            type: type,
            left: pos.x,
            top: pos.y,
            width: self.originWidth,
            height: self.originHeight,
            angle: 0,
            isFrozenSty: false,   //style
            zIndex: 1
          }

          let temp;
          if (type === "image") {
            temp = {
              bgImg: "",
            }
            Object.assign(basic, temp)
          }
          if (type === "text") {
            temp = {
              text: "",
              fontFamily: "Microsoft YaHei",
              fontSize: 20,
              letterSpacing: 2,
              lineHeight: 15,
              color: "#000",
              textAlign: "center",
              bold: false,
              italic: false,
              underline: false,
              fontStyFrozen: false,
            }
            Object.assign(basic, temp)
          }

          //seatpanel
          self.$store.commit("printingDesign/addSeat2Panel", basic);
        }
      }
    });
  }
}
</script>