<template>
  <div v-if="showTool" class="right-tool-box">
    <div class="box-item" @click="showGroup(0)">
      <el-popover
        :value="groupVisible[0]"
        placement="left-start"
        width="360"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          
          <i class="el-icon-arrow-right" @click="closeGroup(0)"></i>
        </div>
        <div class="inner">
          <div class="title">
            <label class="label-box"></label>
            <span v-text="getInfoTypeName()"></span>
          </div>
          <div class="inner-item">
            <label class="label-box">X</label>
            <el-input-number
              :value="info.left"
              size="mini"
              :controls="false"
              @change="v => info.left = v"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box">Y</label>
            <el-input-number
              :value="info.top"
              size="mini"
              :controls="false"
              @change="v => info.top = v"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box"></label>
            <el-input-number
              :value="info.width"
              size="mini"
              :min="0"
              :controls="false"
              @change="v => updateWidth(v)"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box"></label>
            <el-input-number
              :value="info.height"
              size="mini"
              :min="0"
              :controls="false"
              @change="v => updateHeight(v)"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box"></label>
            <el-input-number
              :value="info.angle"
              size="mini"
              :min="0"
              :max="360"
              :controls="false"
              @change="v => info.angle = v"
            ></el-input-number>
          </div>
        </div>
        <i slot="reference" class="iconfont icon-resize-"></i>
      </el-popover>
    </div>
    <div class="box-item" v-if="info.type === 'text'" @click="showGroup(1)">
      <el-popover
        :value="groupVisible[1]"
        placement="left"
        width="360"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          
          <i class="el-icon-arrow-right" @click="closeGroup(1)"></i>
        </div>
        <div class="inner">
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <el-select :value="info.fontFamily" @change="v => {info.fontStyFrozen=false; info.fontFamily = v}">
              <el-option
                v-for="(item, it) in fontFamilyList"
                :value="item.val"
                :label="item.text"
                :key="item.val"></el-option>
            </el-select>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <el-color-picker :value="info.color" @active-change="v => {info.fontStyFrozen = false; info.color=v}"></el-color-picker>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <div class="slider-warp">
              <el-slider
                :value="info.fontSize"
                show-input
                :show-input-controls="false"
                @input="v => {info.fontStyFrozen = false; info.fontSize = v}"></el-slider>
            </div>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <div class="slider-warp">
              <el-slider
                :value="info.lineHeight"
                show-input
                :show-input-controls="false"
                :min="10"
                :max="50"
                @input="v => {info.fontStyFrozen = false; info.lineHeight = v}"></el-slider>
            </div>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <div class="slider-warp">
              <el-slider
                :value="info.letterSpacing"
                show-input
                :show-input-controls="false"
                :max="50"
                @input="v => {info.fontStyFrozen = false; info.letterSpacing = v}"></el-slider>
            </div>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <el-button-group>
              <el-button size="mini" :class="{active: info.italic}" @click="v => {info.fontStyFrozen = false; info.italic = !info.italic}">
                <dsf-icon name="zitixieti"></dsf-icon>
              </el-button>
              <el-button size="mini" :class="{active: info.bold}" @click="v => {info.fontStyFrozen = false; info.bold = !info.bold}">
                <dsf-icon name="zitijiacu"></dsf-icon>
              </el-button>
              <el-button size="mini" :class="{active: info.underline}" @click="v => {info.fontStyFrozen = false; info.underline = !info.underline}">
                <dsf-icon name="zitixiahuaxian"></dsf-icon>
              </el-button>
            </el-button-group>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <el-button-group>
              <el-button size="mini" :class="{active: info.textAlign === 'left'}" @click="v => {info.fontStyFrozen = false; info.textAlign = 'left'}">
                <dsf-icon name="zuoduiqi"></dsf-icon>
              </el-button>
              <el-button size="mini" :class="{active: info.textAlign === 'center'}" @click="v => {info.fontStyFrozen = false; info.textAlign = 'center'}">
                <dsf-icon name="juzhongduiqi"></dsf-icon>
              </el-button>
              <el-button size="mini" :class="{active: info.textAlign === 'right'}" @click="v => {info.fontStyFrozen = false; info.textAlign = 'right'}">
                <dsf-icon name="youduiqi"></dsf-icon>
              </el-button>
            </el-button-group>
          </div>
          <div class="inner-item item-fill-row">
            <label class="label-box"></label>
            <el-input type="textarea" :rows="4" v-model="info.text"></el-input>
          </div>
        </div>
        <i slot="reference" class="iconfont icon-xingzhuang-wenzi"></i>
      </el-popover>
    </div>
    <div class="box-item" v-if="info.type === 'image'" @click="showGroup(2)">
      <el-popover
        :value="groupVisible[2]"
        placement="left-start"
        width="365"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          
          <i class="el-icon-arrow-right" @click="closeGroup(2)"></i>
        </div>
        <div class="inner">
          <div class="single-upload-box" @click="uploadSingle">
            <img v-if="info.bgImg" :src="getImagePath(info.bgImg)" alt="">
          </div>
        </div>
        <i slot="reference" class="iconfont icon-tupiantianjia"></i>
      </el-popover>
    </div>
  
  </div>
</template>

<script>
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/designer/seat/constant";
import {
  uploadImg,
  createUploadFileInput
} from "../func"
import {mapState} from "vuex"
  
export default {
  name: "RightTools",
  props: {},
  data() {
    return {
      showTool: false,
      info: {},
      groupVisible: [false, false, false],
      visiblePointer: null,
      oldVisibleStatus: false,
      fontFamilyList: [
        {val: "SimSun", text: ""},
        {val: "FangSong", text: ""},
        {val: "KaiTi", text: ""},
        {val: "SimHei", text: ""},
        {val: "Microsoft YaHei", text: ""},
        {val: "YouYuan", text: ""},
        {val: "LiSu", text: ""},
      ]
    }
  },
  computed: {
    ...mapState("printingDesign", {
      activeId: "activeId",
    }),
  },
  watch: {
    activeId(val) {
      if (!val) {
        this.showTool = false;
        const arr = new Array(3);
        this.groupVisible = _.fill(arr, false);
      } else {
        eventBus.emit(eventBusEvent.GET_ACTIVE_SEAT, seat => {
          this.info = seat;
        });
          
        this.showTool = true;
        this.$nextTick(() => {
          this.$set(this.groupVisible, this.visiblePointer, this.oldVisibleStatus);
        })
      }
    },
      
    info: {
      handler(val) {
        this.$store.commit("printingDesign/changeSeatInfo", val);
      },
      deep: false
    }
  },
  methods: {
    uploadSingle() {
      const self = this;
      let upDOM = createUploadFileInput();
      $(upDOM).on("change", function () {
        if (!upDOM.files || upDOM.files.length === 0) return false;
          
        const formData = new FormData();
        formData.append("file", upDOM.files[0]);
        const callback = result => {
          //input
          $(upDOM).val("");
          $(upDOM).remove();
          upDOM = null;
            
          if (result === "error") return false;
          self.info.bgImg = result[0].relativePath;
        };
        uploadImg.call(self, formData, callback);
      })
      $(upDOM).click();
    },
    closeGroup(pointer) {
      this.visiblePointer = pointer;
      this.$set(this.groupVisible, pointer, false);
      this.oldVisibleStatus = false;
    },
    showGroup(pointer) {
      this.visiblePointer = pointer;
      const arr = new Array(3);
      this.groupVisible = _.fill(arr, false);
      this.$set(this.groupVisible, pointer, true);
      this.oldVisibleStatus = true;
    },
    getImagePath(path) {
      return path ? dsf.url.getWebPath(path) : null;
    },
    getInfoTypeName() {
      switch (this.info.type) {
      case 'text':
        return ''
      case 'image':
        return ''
      }
    },
    updateWidth(width) {
      this.info.width = width;
    },
    updateHeight(height) {
      this.info.height = height;
    },
  }
}
</script>