<template>
  <div class="designer_tools">
    <DsfButton
      v-for="(button, $index) in layout.buttons"
      :key="$index"
      :icon="button.icon"
      :text="button.text"
      btn-style="icon-text"
      @click="button.handler($event, button)"
    />
    
    <div class="switch-warp">
      <el-switch
        :value="showBorderLine"
        @change="changeShowBorder"
      />
      <span :class="{active: showBorderLine}"></span>
    </div>
    
    <width-input :canvas-set="canvasSet" :has-bg="hasBg"></width-input>
  </div>
</template>

<script>
import html2canvas from 'html2canvas';
import {createUploadFileInput, uploadImg} from '../func'
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/designer/seat/constant";
import {mapState} from 'vuex';
import widthInput from "_platform/designer/seat/printingDesign/widthInput";
  
export default {
  name: "TopTools",
  props: {
    backgroundImage: {
      type: Object,
      default() {
        return {
          url: "",
          width: 0,
          height: 0,
        }
      }
    },
    canvasSet:{
      type: Object,
      default() {
        return {
          width: undefined,
          height: undefined,
        }
      }
    },
    hasBg: {
      type: Boolean,
      default: false
    },   //
  },
  components: {
    widthInput,
  },
  data() {
    return {
      //
      normalButtons: [
        {text: "", icon: "baocun", handler: this.saveLayout},
        {text: "", icon: "tupiantianjia", handler: this.updateBG},
        {text: "", icon: "shuaxin", handler: this.restoreLayout},
        {text: "", icon: "zhiding1", handler: this.levelUp},
        {text: "", icon: "zhidi", handler: this.levelDown},
        {text: "", icon: "shangyibu1", handler: this.backStep},
        {text: "", icon: "yunduanxiazai", handler: this.exportImage},
      ],
      layout: {
        buttons: [],
      },
    }
  },
    
  computed: {
    ...mapState("printingDesign", [
      "compelPanelSize",
      "showBorderLine"
    ])
  },
  watch: {
    "canvasSet.width"(val) {
      if (!this.hasBg) return;
      if (!val) {
        this.$nextTick(() => {
          let boxWid = $(".seat-designer-page").width();
          let scale = boxWid / this.backgroundImage.width;
          this.canvasSet.height = this.backgroundImage.height * scale;
        });
        return;
      }
      let scale = val / this.backgroundImage.width;
      this.canvasSet.height = this.backgroundImage.height * scale;
    },
  },
  created() {
    this.layout.buttons = this.normalButtons;
  },
  methods: {
    backStep() {
    },
    saveLayout() {
      this.$emit("save-layout")
    },
    restoreLayout() {
      const panelSize = {
        width: this.compelPanelSize.width,
        height: this.compelPanelSize.height,
        scale: 1,
        left: 0,
        top: 0,
      }
      this.$store.commit("printingDesign/changePanelSize", panelSize);
    },
    updateBG() {
      const self = this;
      let upDOM = createUploadFileInput("dsf-seat-bg-upload");
      $(upDOM).on('change', function () {
        if (!upDOM.files || upDOM.files.length === 0) return false;
        const formData = new FormData();
        formData.append("file", upDOM.files[0]);
        const callback = result => {
          //input
          $(upDOM).val("");
          $(upDOM).remove();
          upDOM = null;
            
          if (result === "error") {
            self.$emit("update:hasBg", false);
            return false;
          }
          setBgImage2Panel.call(self, result[0]);
        };
        uploadImg.call(self, formData, callback)
      })
      $(upDOM).click();
    },
    levelUp(){
      eventBus.emit(eventBusEvent.LEVEL_UP)
    },
    levelDown(){
      eventBus.emit(eventBusEvent.LEVEL_DOWN)
    },
    changeShowBorder(val){
      this.$store.commit("printingDesign/changeShowBorderLine", val)
    },
    exportImage(){
      const loader = dsf.layer.loading();
      $(".seat-designer-page .seat-box.border-dash").addClass("border-none");
      const panelEl = document.getElementById("panel_outer")
      html2canvas(panelEl).then(function(canvas) {
        const dataUrl = canvas.toDataURL("image/png")
        const a = document.createElement("a");
        a.style.display = "none"
        a.download = ".png"
        a.href = dataUrl;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        $(".seat-designer-page .seat-box.border-dash").removeClass("border-none");
        dsf.layer.closeLoading(loader);
      });
    }
  }
}
  
function setBgImage2Panel(data) {
  const self = this;
  let image = new Image();
  image.src = dsf.url.getWebPath(data.relativePath);
  image.onload = function () {
    self.$emit("update:hasBg", true);
    self.$emit("update:backgroundImage", {
      width: image.naturalWidth,
      height: image.naturalHeight,
      relativePath: data.relativePath,
      absolutePath: data.absolutePath
    })
  };
  image.onerror = function () {
    dsf.layer.message("", false);
    self.$emit("update:hasBg", false);
  }
}

</script>